/* eslint-disable no-unused-vars */
import { USER_LOGIN_REQUEST, USER_LOGIN_SUCCESS, USER_LOGIN_FAIL, USER_REGISTER_REQUEST,
    USER_REGISTER_SUCCESS, USER_REGISTER_FAIL, USER_LOGOUT , USER_DETAILS_RESET,
    USER_DETAILS_REQUEST,USER_DETAILS_SUCCESS,USER_DETAILS_FAIL
} from "../../contants/userContants";
import axios from "axios";


export const logout = () => (dispatch) => {
    localStorage.removeItem("userInfo");
    dispatch({ type: USER_LOGOUT });
    dispatch({ type: USER_DETAILS_RESET });
    // navigate("/login")
};

//LOGIN
export const login = (email, password) => async (dispatch) => {
 
    try {
        dispatch({ type: USER_LOGIN_REQUEST })
        // const config= {headers:{'content-Type':'application/json'}};
        const { data } = await axios.post("https://api-survey-2.fewnu.app/api/login",
            { email, password },
            // config 
        );
        
            dispatch({
                type: USER_LOGIN_SUCCESS,
                payload: data,
            });
        
         
        localStorage.setItem('userInfo', JSON.stringify(data))
    } catch (error) {
        dispatch({
            type: USER_LOGIN_FAIL,
            payload:
                error.response && error.response.data.message
                    ? error.response.data.message
                    : error.message,
        });
    }
}

//register
export const register = (name, email, password) => async (dispatch) => {
    try {
        dispatch({ type: USER_REGISTER_REQUEST })
        //const config= {headers:{'content-Type':'application/json'}};
        const { data } = await axios.post("https://api-survey-2.fewnu.app/api/users/register",
            { name, email, password },
            //config 
        );
        dispatch({
            type: USER_REGISTER_SUCCESS,
            payload: data,
        });
        dispatch({
            type: USER_LOGIN_SUCCESS,
            payload: data,
        });
        localStorage.setItem('userInfo', JSON.stringify(data))
    } catch (error) {
        dispatch({
            type: USER_REGISTER_FAIL,
            payload:
                error.response && error.response.data.message
                    ? error.response.data.message
                    : error.message,
        });
    }
};

//User DETAILS
export const getUserDetails = (id) => async (dispatch, getState) => {
    
    try {
        dispatch({
            type: USER_DETAILS_REQUEST
        });
        const {
            userLogin: {userInfo},
        } = getState();
        const config = {
            headers: { 
                // 'content-Type': 'application/json',
                Authorization: `Bearer ${userInfo.token}`
            },
        };
        const { data } = await axios.get(`https://api-survey-2.fewnu.app/api/user/${id}`, config)
        dispatch({
            type: USER_DETAILS_SUCCESS,
            payload: data,
        });
    } catch (error) {
        const message =
        error.response && error.response.data.message
        ? error.response.data.message
        : error.message;
        if (message=== "Not authorized, token failed"){
            dispatch(logout());
        }
        dispatch({
            type: USER_DETAILS_FAIL,
            payload: message,
        });
    }
};